// utils/collectionPriceCalculator.js

import { calculateFinalPrice } from "./price";

/**
 * Calculate price for a collection item based on its description HTML
 * @param {Object} item - Collection item with descriptionHtml
 * @param {string} defaultKarat - Default gold karat (e.g., "18K")
 * @returns {Promise<string>} - Formatted total price
 */
export async function calculateCollectionItemPrice(item, defaultKarat = "18K") {
  try {
    const descriptionHtml = item.descriptionHtml;

    if (!descriptionHtml) {
      console.warn(
        `‚ö†Ô∏è No descriptionHtml for ${item.handle}, using fallback price: ‚Çπ${item.price}`
      );
      return item.price || "0.00";
    }

    // Parse the description HTML
    const parser = new DOMParser();
    const doc = parser.parseFromString(descriptionHtml, "text/html");
    const liElements = doc.querySelectorAll(".product-description ul li");

    if (liElements.length === 0) {
      console.warn(
        `‚ö†Ô∏è No product specs found in description for ${item.handle}`
      );
      return item.price || "0.00";
    }

    // Build spec map
    const specMap = {};
    liElements.forEach((li) => {
      const key = li
        .querySelector("strong")
        ?.textContent.replace(":", "")
        .trim();
      const value = li.textContent
        .replace(li.querySelector("strong")?.textContent || "", "")
        .trim();
      if (key && value) specMap[key] = value;
    });

    console.log(`üìä Specs for ${item.handle}:`, specMap);

    // Extract diamond data
    const shapes =
      specMap["Diamond Shape"]?.split(",").map((v) => v.trim()) || [];
    const weights =
      specMap["Diamond Weight"]?.split(",").map((v) => v.trim()) || [];
    const counts =
      specMap["Total Diamonds"]?.split(",").map((v) => v.trim()) || [];

    const diamonds = shapes.map((shape, i) => ({
      shape,
      weight: parseFloat(weights[i]) || 0,
      count: parseInt(counts[i]) || 0,
    }));

    // Extract gold weight for the specified karat
    const goldWeightKey = Object.keys(specMap).find((key) =>
      key.toLowerCase().includes(defaultKarat.toLowerCase())
    );
    const goldWeight = parseFloat(specMap[goldWeightKey]) || 0;

    console.log(`üíé Calculating price for ${item.handle}:`, {
      diamonds,
      goldWeight,
      goldKarat: defaultKarat,
    });

    // Calculate final price
    const result = await calculateFinalPrice({
      diamonds,
      goldWeight,
      goldKarat: defaultKarat,
    });

    console.log(
      `‚úÖ Calculated price for ${item.handle}: ‚Çπ${result.totalPrice}`
    );
    return result.totalPrice.toFixed(2);
  } catch (error) {
    console.error(`‚ùå Error calculating price for ${item.handle}:`, error);
    return item.price || "0.00";
  }
}

/**
 * Calculate prices for multiple collection items in batch
 * @param {Array} items - Array of collection items
 * @param {string} defaultKarat - Default gold karat
 * @returns {Promise<Object>} - Object mapping item handles to prices
 */
export async function calculateBatchPrices(items, defaultKarat = "18K") {
  console.log(
    `üîÑ Starting batch price calculation for ${items.length} items...`
  );
  const prices = {};

  // Calculate prices in parallel for better performance
  await Promise.all(
    items.map(async (item) => {
      const price = await calculateCollectionItemPrice(item, defaultKarat);
      prices[item.handle] = price;
    })
  );

  console.log(`‚úÖ Batch calculation complete!`, prices);
  return prices;
}
